body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0%;
}
:root{
  /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
  font-family: 'Rozha One', serif;
}
::-webkit-input-placeholder{
  color: rgb(127, 114, 162) ;
}
:-ms-input-placeholder{
  color: rgb(127, 114, 162) ;
}
::placeholder{
  color: rgb(127, 114, 162) ;
}




header{
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  background: rgb(12, 0, 42);
  color: rgb(127, 114, 162);;
  font-weight: bold;
  font-family: "Britannic";
  overflow-x: hidden;
  position: fixed;
  z-index: 10;
}

.left {
  padding-left: 20px;
  cursor: pointer;
  font-weight: 900;
  margin-right: 20px;
  display: none;
}

.middle{
  overflow: auto;
}
.middle::-webkit-scrollbar{
  width: 0px;
  height: 3.5px;
}
.middle::-webkit-scrollbar-thumb{
  background-color: inherit
}
.middle:hover::-webkit-scrollbar-thumb{
  background-color: #7F72A2;
}
.middle ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 880px;
}
.middle .menus {
  padding: 0px 20px;
  letter-spacing: 1.5px;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}
.middle .menus:hover{
  color:rgb(58, 252, 255) ;
}
.middle .menus:focus{
  color: rgb(211, 211, 211);
}

.right{
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  padding-right: 20px;
}
.toggle_btn{
  border: 1px solid rgb(127, 114, 162);
  width: 60px;
  height: 30px;
  border-radius: 20px;
  cursor: pointer;
}
.toggle_btn:hover .toggle_switch{
  -webkit-animation: toggle_hover 0.5s;
          animation: toggle_hover 0.5s;
}
.toggle_switch{
  height: 30px;
  width: 30px;
  background:rgb(127, 114, 162);
  border-radius: 100%;
  transition: 1s;
}
@-webkit-keyframes toggle_hover{
  0%{
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
  33%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  64%{
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  
}
@keyframes toggle_hover{
  0%{
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
  33%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  64%{
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  
}
.menubtn svg {
  cursor: pointer;
  /* margin-right: 20px; */
  display: none;
}



.news_items_page{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 1200px;
  margin: auto;
  transition: 0.5s;
}
.news_items_page h1{
  font-size: 28px;
  font-weight: 600;
  padding: 0 20px;
  text-align: center;
  margin: 40px;
  margin-top: 70px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.news_items{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: 0.5s;

}




.note {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 320px;
  min-width: 20%;
  padding-bottom: 20px;
  box-shadow: 2px 2px 10px gray;
  border-radius: 10px;
  margin: 15px;
  overflow-x: hidden;
  transition: 0.5s;

}
.note_image{
  width: 100%;
  margin: auto;
  transition: 0.5s;
  position: relative;
}
.note_image img{
  width: 100%;
  /* border-top-left-radius: 10px;
  border-top-right-radius: 10px; */
  transition: 0.5s;

}
.note_source{
  position:absolute;
  top: 10%;
  /* right: 0%;
   */
  background-color: red;
  padding: 2px 15px;
  font-size: 13px;
  font-weight: 600;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.note_title {
  font-size: 17px;
  font-weight: bold;
  margin: 7px 0;
  color: rgb(32, 84, 2);
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  padding: 0 10px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.note_desc {
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  padding: 0 10px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.note_other{
  font-size: 13px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-style: italic;
  text-align: left;
  width: 90%;
  margin-bottom: 15px;
  color: gray;
}
.drbtn {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
}
#readbtn,#prevbtn,#nextbtn{
  padding: 10px;
  width: 80px;
  border: none;
  border-radius: 100px;
  border-bottom-right-radius: 0px;
  font-size: 14px;
  font-weight: bold;
  font-family: "Copperplate Gothic";
  color: rgb(235, 219, 0);
  background: linear-gradient(45deg, rgb(0, 64, 44), rgb(0, 99, 68), rgb(1, 59, 41));
  cursor: pointer;
}
#readbtn a {
  color: rgb(235, 219, 0);
}




.next_prev_btns{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1200px;
  margin:40px;
  transition: 0.5s;
  width: 100%;
}
.next_btn,.prev_btn{
  padding: 10px;
  width: 80px;
  border: none;
  border-radius: 100px;
  border-bottom-right-radius: 0px;
  font-size: 14px;
  font-weight: bold;
  font-family: "Copperplate Gothic";
  color: rgb(235, 219, 0);
  background: linear-gradient(45deg, rgb(0, 64, 44), rgb(0, 99, 68), rgb(1, 59, 41));
  cursor: pointer;
  transition: 0.5s;

}
.prev_btn{
  border-bottom-right-radius: 100px;
  border-bottom-left-radius: 0px;
}
.next_btn:disabled,.prev_btn:disabled{
  opacity: 0.5;
  cursor: unset;
}




.spinner{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: 0.5s;
  margin: 40px auto;
}
.spinner img{
  width: 150px;
  transition: 0.5s;
}



.toploader{
  width: 0vw;
  height: 3px;
  background-color: red;
  position: fixed;
  top: 51px;
  transition:0.5s;
}
.toploader_progress1{
  width: 100vw;
  height: 3px;
  background-color: red;
  position: fixed;
  top: 51px;
  transition:0.5s;
  -webkit-transform: translateX(80%);
          transform: translateX(80%);
}













